.form-control {
  position: relative;
  height: 40px;
  color: #cccccc;
  background-color: #000123;
  border-radius: 0%;
  border: none !important;
  clip-path: polygon(
    3% 0,
    80% 0%,
    100% 0,
    100% 80%,
    97% 100%,
    20% 100%,
    0 100%,
    0% 20%
  );
}

.form-control::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 42px;
  background-color: #3c3d52 !important;
  clip-path: polygon(
    3% 0,
    80% 0%,
    100% 0,
    100% 80%,
    97% 100%,
    20% 100%,
    0 100%,
    0% 20%
  );
}

.form-control:focus {
  color: #e8e8e8;
  background-color: #000123 !important;
  border-color: #3c3d52 !important;
  outline: 0;
  box-shadow: none !important;
}

.input_message {
  height: 100px;
  background: #000123;
  border-top-left-radius: 8px;
  border-bottom-right-radius: 10px;
}

.form-control::placeholder {
  color: #fff;
  opacity: 0.4;
  font-size: 14px;
  font-weight: normal !important;
}

.vl {
  border-left: 6px solid green;
  height: 600px;
}
/*==========================================
            Modal CSS          
  ==========================================*/

.btn-close {
  width: 20px;
  height: 20px;
  font-size: 12px;
  background-color: rgb(213 213 213 / 50%);
  border-radius: 4px;
  position: relative;
  z-index: 3;
}

.btn-close:hover,
.home-modal .btn-close:focus {
  opacity: 0.5;
  outline: none !important;
  box-shadow: none !important;
}

.modal-title {
  color: #1693d6 !important;
  font-family: "Jost", sans-serif;
  font-weight: 700;
  font-size: 24px;
}

.pro_modal_destails {
  font-size: 18px;
  margin-bottom: 0 !important;
}

.modal_btn {
  padding: 6px 20px;
}

/*==========================================
        Hover Effects Buttons CSS          
  ==========================================*/

.btn_custom.hover-slide-right::before {
  top: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 0%;
}

.btn_custom.hover-slide-right:hover::before {
  width: 100%;
  background-color: #000;
}

.btn-2::before {
  transition: 0.3s ease-out;
  background-image: linear-gradient(to left, #3d1cd9, #fc6717);
}
.btn-2 span {
  color: rgb(28, 31, 30);
  transition: 0.2s;
}
.btn-2 span:hover {
  color: #fff;
  transition: 0.2s 0.1s;
}

.btn_outline_custom.hover-slide-right::after {
  top: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 0%;
}

.btn_outline_custom.hover-slide-right:hover::after {
  width: 100%;
  background-color: #000;
}

.btn_2::after {
  transition: 0.3s ease-out;
}
.btn_2 span {
  color: rgb(28, 31, 30);
  transition: 0.2s;
}
.btn_2 span:hover {
  color: #fff;
  transition: 0.2s 0.1s;
}
