/*==========================================
       navbar media query     
  ==========================================*/
@media only screen and (min-width: 1200px) and (max-width: 1399px) {
  .navbar-custom .navbar-nav li a {
    margin: 0 3px;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 0px !important;
    padding-left: 0px !important;
  }

  .brand_logo img {
    height: 37px !important;
  }

  .navbar-custom .navbar-nav li a {
    font-size: 12px;
    margin: 0px 7px;
  }
}

@media (min-width: 992px) and (max-width: 1236px) {
  .navbar-custom .navbar-nav {
    margin-left: 0px;
  }
}

@media only screen and (min-width: 992px) and (max-width: 2700px) {
  .dropdown_head1 {
    display: none;
  }

  .dropdown_head2 {
    display: block;
  }

  .nav_bg_head {
    background-image: none;
  }
}

@media only screen and (min-width: 200px) and (max-width: 991px) {
  .dropdown_head1 {
    display: block;
  }

  .dropdown_head2 {
    display: none;
  }

  .nav_bg {
    display: none;
  }

  .navbar-custom {
    background-color: #000123;
  }

  .nav_bg_head {
    position: fixed;
    background-image: url("../images/nav_sm_bg1.svg"),
      url("../images/nav_sm_bg2.svg");
    background-position: top 0px left -16px, top 0px right -46px;
    background-repeat: no-repeat, no-repeat;
    background-size: auto;
    background-blend-mode: lighten;
    width: 100%;
    height: auto;
  }

  .lighten_bg {
    mix-blend-mode: plus-lighter !important;
  }
}

@media only screen and (min-width: 375px) and (max-width: 450px) {
  .brand_logo img {
    width: 75% !important;
    padding: 0 0px !important;
    margin: auto !important;
    display: flex;
    justify-content: center;
  }

  .navbar-brand {
    margin: 0 !important;
  }

  .btn_custom_dropdown {
    padding: 3px 11px !important;
  }

  .footer .btn_custom_dropdown {
    padding: 10px 11px !important;
  }
}

@media only screen and (min-width: 200px) and (max-width: 374px) {
  .brand_logo img {
    width: 60% !important;
    padding: 0 0px !important;
    margin: 0 !important;
  }

  .navbar-toggler {
    padding: 0 !important;
  }

  .navbar-brand {
    margin: 0 !important;
    display: contents;
  }

  .footer .btn_custom_dropdown {
    padding: 10px 11px !important;
  }

  .btn_custom_dropdown {
    padding: 3px 11px !important;
  }

  .footer .btn_custom_dropdown {
    font-size: 13px;
  }
}





